body{
  
  width: 100%;
  height: 100vh;
  overflow: auto;
  background: linear-gradient(315deg, rgb(166, 84, 161) 3%, rgb(108, 167, 226) 38%, rgb(79, 124, 120) 68%, rgb(73, 154, 111) 88%);
  animation: gradient 15s ease infinite;
  background-size: 400% 400%;
  background-attachment: fixed;
}

@keyframes gradient {
  0% {
      background-position: 0% 0%;
  }
  50% {
      background-position: 100% 100%;
  }
  100% {
      background-position: 0% 0%;
  }
}



